import React, { useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import FormLabel from '@material-ui/core/FormLabel';
import { makeStyles, Theme } from '@material-ui/core/styles';
import moment from 'moment'

import CheckCircleOutlineOutlinedIcon from '@material-ui/icons/CheckCircleOutlineOutlined';
import { CurrencyField, SelectTimeField } from '../../../../../components/Fields';
import { StarsBackgroundPath } from '../../../../../components/Images';
import STRINGS from '../../../../../strings';
import { LaboralEntity, TermLoan, LaboralSector, Subgrupo_inst } from './index';
import { Props as QuotationProps } from '../index';

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    display: 'flex', 
    flexDirection: 'column', 
    height: '100%', 
    paddingTop: 72, 
    paddingBottom: 72,
    [theme.breakpoints.down('sm')]: {
      paddingTop: 24, 
      paddingBottom: 24
    }
  },
  title: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: 72
  },
  subtitle: {
    '& span': {
      fontWeight: theme.typography.fontWeightBold,
      color: theme.palette.primary.main
    }
  },
  content: {
    textAlign:'center',
    marginBottom: 80
  },
  icon: {
    color: theme.palette.success.main,
    height: 120,
    width: 120,
    marginBottom: 36
  },
  label: {
    marginBottom: 24
  },
  starsBackground: {
    backgroundImage: `url(${StarsBackgroundPath})`,
    backgroundSize: 'contain',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat'
  }
}));
    
interface Props extends QuotationProps {
  laboralEntitys: LaboralEntity[];
  termsLoan: TermLoan[];
  sectors: LaboralSector[];
  subgrupo_inst: Subgrupo_inst[];
};

let loan = 0.00
let financ1 = 0.00
let plazo = 0

function Form(props: Props) {
  const { quotation, setQuotation, termsLoan, laboralEntitys, sectors, subgrupo_inst } = props;
  const classes = useStyles();

  let { monthlyResidenceFee, wage, alloance, perDiem, jobSector, profession = 0, occupation, institution = 0, currentJobMonths = 0, contractType = "" } = quotation.borrower

  const idSectors = sectors.filter(st => st.sector === jobSector)
  const idSector = idSectors[0].id_sector
  const idEntity = '100'  // Tarifa Base o Standard

  const keyTarifas = {
    'Sector': jobSector,
    'idSector': idSector,
    'Profesion': profession,
    'Ocupacion': occupation,
    'Institucion': institution,
    'Tiempo de Trabajo (meses)': currentJobMonths,
    'Status Laboral': contractType,
  }

  //  Status Laboral
  //  0	Temporal	
  //  1	Permanente	
  //  2	Serv. Profesional	


  let subgrupo = 0
  // Sector Gobierno
  if(institution !== 0 && idSector === 2) {
    // Buscar subgrupo (1 o 2)
    subgrupo = 1
  }

  // Sector Privado
  if(profession === "1" || profession === "2" || profession === "3") {
    subgrupo = 10
  }

  // Sector ACP
  if(profession === "5") {
    subgrupo = 21
    if(contractType === "0") subgrupo = 20
  }

  // Sector Especialistas
  if((profession === "2" || profession === "3") && idSector === 2) {
    subgrupo = 30
  }

  // Sector Jubilados
  if(profession === "7") {
    subgrupo = 40
  }

  console.log(subgrupo)
  console.log(subgrupo_inst)
  console.log(quotation)
  const subgrupo_inst_wrk = subgrupo_inst.filter(le => le.id === subgrupo)
  console.log(subgrupo_inst_wrk)

  let { gender, birthDate } = quotation.borrower;

  const today = new Date() ; //  Ver como traerla del servidor
  let birth = new Date();
  if (birthDate !== undefined) {
    birth = new Date(birthDate)
  }

  let dd = today.getDate()
  let mm = today.getMonth() + 1
  let yyyy = today.getFullYear() 

  let d1 = dd<10?'0'+dd.toString():dd.toString()
  let m1 = mm<10?'0'+mm.toString():mm.toString()
  // Fecha de Cotizacion
  const f1 = moment(yyyy.toString()+m1.toString()+d1.toString())

  dd = birth.getDate()
  mm = birth.getMonth() + 1
  yyyy = birth.getFullYear() + (gender==='male' ? 61 : 56)
  const age79 = 79 - (today.getFullYear() - birth.getFullYear() - 1)
  const age85 = 85 - (today.getFullYear() - birth.getFullYear() - 1)

  d1 = dd<10?'0'+dd.toString():dd.toString()
  m1 = mm<10?'0'+mm.toString():mm.toString()
  // Fecha de Jubilacion
  let f2 = moment(yyyy.toString()+m1.toString()+d1.toString())
  // Fecha en la que se llega a los 79
  let f3 = moment((today.getFullYear()+age79).toString()+m1.toString()+d1.toString())

  // const objFechas = {'Hoy es =>':f1,'FJubilacion -1 =>':f2, 'Aux Jub age79 =>': f3, 'age79': age79}
  // console.log(objFechas,yyyy.toString()+m1.toString()+d1.toString(),birth.getFullYear());

  const laboralEntities = laboralEntitys.filter(
    le => le.id_sector === idSector && 
    le.id_profesion === Number(profession) &&
    le.ruta === idEntity)


  // Validar para saber si es Financomer
  let { entity = '' } = quotation.finance;
  let financ = false
  if(entity === 'financomer') financ = true
  let Edad = parseInt(window.localStorage.getItem('Edad') || '0');
  let anios = subgrupo_inst_wrk[0].anios;

  // console.log('laboralEntities',laboralEntitys,idSector,profession,idEntity)
  let { 
    comision = 0,
    debt_capacity = 0,
    debt_capacity_mortgage = 0,
    discount_capacity = 0,
    discount_capacity_mortgage = 0,
    mount_max = 0,
    mount_min = 0,
    plazo_max = 0,
    tasa = 0 } = laboralEntities[0] 

  if(financ){
    plazo_max = subgrupo_inst_wrk[0].plazo_maximo
  }

  const financieras = {
    'financomer': {
      FECI_mensual: profession === "7" ? 0: 0.000833333,
      plazo_max: subgrupo_inst_wrk[0].plazo_maximo,
      cap_descto_available: 0.00,
      fvencto: new Date(),
      tasaMes: 0.00,
      amount: 0.00
    }
  } 

  let fj = new Date(yyyy.toString()+'-'+m1.toString()+'-'+d1.toString())
  if (jobSector === 'J') {
    f2 = f3
    fj = new Date((today.getFullYear()+age79).toString()+'-'+m1.toString()+'-'+d1.toString())
  }
  dd = f2.diff(f1,'days')
  let plazo_age = Math.floor(dd/30.5)

  let isOlder = true
  if(plazo_max <= plazo_age) isOlder = false


  function handleCapacity(plazoCalc: number) {

    let salary = wage ? wage : 0
    if(alloance !== undefined) salary +=  alloance
    if(perDiem !== undefined) salary +=  perDiem

    const segVida = 0.1   // Pendiente definicion
    const feci = 1.0      // Pendiente definicion
    let wFeci = 0.0
    const itbms = 7.0     // Pendiente definicion
    const Notaria =	3.00

    let mortage = monthlyResidenceFee ? monthlyResidenceFee : 0.00

    let pje_dscto = discount_capacity ? discount_capacity : 0.00
    if(mortage > 0) pje_dscto = discount_capacity_mortgage ? discount_capacity_mortgage : 0.00
    let pje_debt = debt_capacity ? debt_capacity : 0.00
    if(mortage > 0) pje_debt = debt_capacity_mortgage ? debt_capacity_mortgage : 0.00

    const cap_discto = salary * pje_dscto / 100.00
    const cap_debt = salary * pje_debt / 100.00

    const cap_debt_available = cap_debt - mortage
    let cap_discto_available = cap_debt_available < cap_discto ? cap_debt_available : cap_discto

    //***************************************************************/
    //***************************************************************/
    //** CALCULO STANDAR PARA LOS BANCOS  **/
    //***************************************************************/
    //***************************************************************/
    console.log('Bancos',plazoCalc, pje_dscto, age79)
    let wrkV = calc_fvcto(plazoCalc, pje_dscto)
    let wPlazo = wrkV.wpp 
    plazoCalc = wPlazo + wrkV.wDic
    plazo = plazoCalc
    const term = wPlazo //plazoCalc

    const daysLoan = Math.round((wrkV.fvencto.getTime() - wrkV.fcot.getTime())/(24*60*60*1000))
    // const avgDaysLoan = Math.round((daysLoan/plazoCalc)*100)/100
    // const paysYear = Math.round((360/avgDaysLoan)*100)/100
    const avgDaysLoan = (daysLoan/plazoCalc)
    const paysYear = (360/avgDaysLoan)
    let tasaMes = tasa/(paysYear ? paysYear : 1)/100
    let factor = (1-(1+tasaMes)**(-plazoCalc))/tasaMes
    const reFactor = tasaMes/(1-(1+tasaMes)**(-plazoCalc))

    let nota = ''
    tasaMes = tasa/(paysYear ? paysYear : 1)/100
    loan = Math.round((factor*cap_discto_available)/(1+(factor*segVida/100*(1+itbms/100))))
    if(loan > 5000) {
      console.log('Loan > 5000 FCI:',loan,feci);
      wFeci = feci
      tasaMes = (tasa+feci)/(paysYear ? paysYear : 1)/100
      factor = (1-(1+tasaMes)**(-plazoCalc))/tasaMes
      loan = Math.round((factor*cap_discto_available)/(1+(factor*segVida/100*(1+itbms/100))))
    }
    
    if(loan > mount_max) {
      nota = `*** Monto del préstamo por encima del Máximo. Monto calculado: ${loan} ***`
      loan = mount_max
    }
    if(loan < mount_min) nota = '*** Monto del préstamo por debajo del mínimo. ***'

    let available = {
      'salary': salary, 
      'mortage': mortage, 
      'comision': comision,
      'plazo': plazoCalc,
      'plazo pagos': wPlazo,
      'tasa': tasa,
      'feci': wFeci,
      'monto_max': mount_max,
      'monto_min': mount_min,
      'pje_dscto': pje_dscto,
      'pje_debt': pje_debt,
      'cap_discto': cap_discto,
      'cap_discto_available': cap_discto_available,
      'cap_debt': cap_debt,
      'cap_debt_available': cap_debt_available,
      'f_cotizacion': wrkV.fcot,
      'f_1er_pago': wrkV.ffpay,
      'f_vencto': wrkV.fvencto,
      'dias_prestamo': daysLoan,
      'avgDaysLoan': avgDaysLoan,
      'paysYear': paysYear,
      'tasaMes': tasaMes,
      'factor': factor,
      're-factor': reFactor,
      'LOAN': loan,
      'Fiancomer': financ1,
      'Observación': nota
    }

    console.log({'Disponible': available})

    //***************************************************************/
    //***************************************************************/
    //** CALCULO STANDAR PARA FINANCIRAS  **/
    //***************************************************************/
    //***************************************************************/
    
    // Fecha en la que se llega a los 85
    f3 = moment((today.getFullYear()+age85).toString()+m1.toString()+d1.toString())

    fj = new Date(yyyy.toString()+'-'+m1.toString()+'-'+d1.toString())
    if (jobSector === 'J') {
      f2 = f3
      fj = new Date((today.getFullYear()+age85).toString()+'-'+m1.toString()+'-'+d1.toString())  
    }
    dd = f2.diff(f1,'days')
    plazo_age = Math.floor(dd/30.5)
  
    isOlder = true
    if(financieras.financomer.plazo_max <= plazo_age) isOlder = false

    console.log('Financieras',financieras.financomer.plazo_max, pje_dscto, plazo_age, age85)
    wrkV = calc_fvcto(financieras.financomer.plazo_max, pje_dscto)
    wPlazo = wrkV.wpp 
    plazoCalc = wPlazo + wrkV.wDic
    const termf1 = wPlazo //plazoCalc
    
    if(financ) plazo = plazoCalc
    financieras.financomer.fvencto = wrkV.fvencto
    const FECI_mensual = financieras.financomer.FECI_mensual

    if(subgrupo === 30 ) { // Especialistas
      if(currentJobMonths >= anios*12) {
        tasaMes = subgrupo_inst_wrk[0].tasa_mes_mayor
      } else {
        tasaMes = subgrupo_inst_wrk[0].tasa_mes_menor
      }
    } else if(subgrupo === 40) { // Jubilados
      if(Edad > anios) {
        tasaMes = subgrupo_inst_wrk[0].tasa_mes_mayor
      } else {
        tasaMes = subgrupo_inst_wrk[0].tasa_mes_menor
      }
    } else {
      if(currentJobMonths >= anios*12) { // Resto
        tasaMes = subgrupo_inst_wrk[0].tasa_mes_mayor
      } else {
        tasaMes = subgrupo_inst_wrk[0].tasa_mes_menor
      }
    }
      
    tasaMes = tasaMes/100
    // financ1 = Math.round(((wPlazo*cap_discto_available)-Notaria)/(1+(plazoCalc*FECI_mensual) + (0.00168*plazoCalc) + (tasaMes*plazoCalc) + 0.2675))
    financ1 = ((wPlazo*cap_discto_available)-Notaria)/(1+(plazoCalc*FECI_mensual) + (0.00168*plazoCalc) + (tasaMes*plazoCalc) + 0.2675)
    console.log(wPlazo,cap_discto_available,Notaria,1,plazoCalc,FECI_mensual,0.00168,plazoCalc,tasaMes,plazoCalc,0.2675)

    financ1 = Math.round(financ1*100)/100
    financieras.financomer.tasaMes = tasaMes
    financieras.financomer.amount = financ1

    available = {
      'salary': salary, 
      'mortage': mortage, 
      'comision': comision,
      'plazo': plazoCalc,
      'plazo pagos': wPlazo,
      'tasa': tasa,
      'feci': wFeci,
      'monto_max': mount_max,
      'monto_min': mount_min,
      'pje_dscto': pje_dscto,
      'pje_debt': pje_debt,
      'cap_discto': cap_discto,
      'cap_discto_available': cap_discto_available,
      'cap_debt': cap_debt,
      'cap_debt_available': cap_debt_available,
      'f_cotizacion': wrkV.fcot,
      'f_1er_pago': wrkV.ffpay,
      'f_vencto': wrkV.fvencto,
      'dias_prestamo': daysLoan,
      'avgDaysLoan': avgDaysLoan,
      'paysYear': paysYear,
      'tasaMes': tasaMes,
      'factor': factor,
      're-factor': reFactor,
      'LOAN': loan,
      'Fiancomer': financ1,
      'Observación': nota
    }
    
    if(financ) loan = financ1

    setQuotation({
      loan: { 
        amount: loan,
        financ1: financ1,
        paysYear: paysYear,
        mount_min,
        mount_max,
        term: term,
        termf1: termf1,
        discount_capacity: cap_discto_available,
        discount_capacity_mortgage,
        debt_capacity,
        debt_capacity_mortgage
      }
    })

    console.log({'Disponible': available})
    console.log({'Key Tarifas': keyTarifas})
  }

  const calc_fvcto = (plazoCalc: number, pje_dscto: number) => {
    const fcot = new Date()
    let ffpay = new Date(fcot.getTime() + ((pje_dscto > 20 ? 60 : 30)*24*60*60*1000))
    let fvencto = ffpay
    let ff = ffpay

    // Datos cargado para probar la formula
    // plazoCalc = 120
    // tasa = 9.75
    // cap_discto_available = 154.05
    
    let yyyy = ffpay.getFullYear()
    let mm = ffpay.getMonth() + 1
    let dd = ffpay.getDate()

    // let yyyy = fcot.getFullYear()
    // let mm = fcot.getMonth() + 1
    // let dd = fcot.getDate()

    const dd2 = dd
    // console.log('FCot: ', fcot, 'Primer Pago: ', ffpay, 'Plazo Max:', plazoCalc, 'Entidad:', financ );
    // console.log("******************************")
    let ftemp = ''

    let wDic = 0
    let wpp = 0
    let wPlazo = plazoCalc

    for(let i=0; i<wPlazo; i++){
      if(mm === 12) {
        mm = 1
        yyyy += 1
        wDic = wDic + 1
        if (isOlder) wPlazo = wPlazo - 1
      } //else mm += 1

      if(dd2 > 30 && (mm === 4 || mm === 6 || mm === 9 || mm === 11 )) dd = 30
      else if(dd2 > 27 && mm === 2) {dd = 28}
      else {dd = dd2}

      ftemp = yyyy.toString() + '-' + ((mm < 10) ? '0'+mm.toString() : mm.toString()) + '-' + ((dd < 10) ? '0'+dd.toString() : dd.toString())

      const f = new Date(ftemp)
      ff = new Date(f.getTime() + 24*60*60*1000)
      // console.log('Break ....')
      if (ff > fj) break

      fvencto = ff
      wpp = wpp + 1

      console.log(i+1, yyyy,mm,dd, fvencto, ff <= fj, wpp);
      
      mm += 1
    }

    return {wpp, wDic, fcot, fvencto, wPlazo, ffpay}
  }

  useEffect(() => {
    handlePlazoTerm()
  }, [])

  function handlePlazoTerm() {
      handleCapacity(plazo_max)
  }

  function handleAmountChange(amount: string) {
    let amt = Number(amount)
    setQuotation({
      loan: { amount: amt },
    });
  }

  function handleTermChange(term: string) {
    if (Number(term) <= plazo_max) {
      setQuotation({
        loan: { term: Number(term) },
      });
      handleCapacity(parseInt(term))
    }
  }

  // const te = termsLoan.filter(t => t.id <= plazo_max)
  // const tw = termsLoan.filter(t => t.id === plazo_max)
  // if(!tw.length){
  //   te.push({id: plazo_max, name: plazo_max})
  // }

  const terms = termsLoan.map((t) => ({
    id: t.id.toString(),
    name: t.name.toString()
  }));


  return (
    <>
       <Grid container justify='center'>
        <Grid item xs={12} sm={5} classes={{item: classes.content}}>
          <div className={classes.starsBackground}>
            <CheckCircleOutlineOutlinedIcon classes={{root: classes.icon}} />
          </div>
          <Typography 
            align='center' 
            variant='h5' 
            paragraph
          >
            {STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.TITLE}
          </Typography>
          <Typography
            align='center' 
            color='textSecondary'
            classes={{root: classes.subtitle}}
          >
            {STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CONTENT(loan, plazo)}
          </Typography>
        </Grid>
      </Grid>
      <Grid container spacing={2} justify='center'>
        <Grid item xs={12}> 
          <Grid item xs={12} classes={{item: classes.label}}>
            <FormLabel>{STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_AMOUNT}</FormLabel>
          </Grid>
          <Grid item xs={12}>
            <CurrencyField
              onChange={handleAmountChange}
              value={quotation.loan.amount}
            />
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <SelectTimeField
            label={STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_TERM}
            value={quotation.loan.term}
            onChange={handleTermChange}
            options={terms}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default Form;