import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

import STRINGS from '../../../../strings';
import { CardRadioButtons } from '../../../../components/RadioButtons';
import { DateField, WeightField, HeightField } from '../../../../components/Fields';
import { MaleIcon, FemaleIcon } from '../../../../components/Svg';

import { HEIGHT_UNITS, WEIGHT_UNITS } from '../types';
import { Props } from './index';

const useStyles = makeStyles({
  bottomSpacing: {
    marginBottom: 40 //MRG_BOTT.bottomSpacing,
  }
});

const options = [{
  label: 'Masculino',
  value: 'male',
  Icon: MaleIcon
}, {
  label: 'Femenino',
  value: 'female',
  Icon: FemaleIcon
}];

function Profile(props: Props) {
  const classes = useStyles();
  const { quotation, setQuotation } = props;

  function onGenderSelected(gender: string): void {
    setQuotation({
      borrower: { gender },
    });
  }

  function onBirthDateSelected(birthDate?: number | null): void {
    if (birthDate) {
      setQuotation({
        borrower: { birthDate },
      });
    }
  }

  function onHeightChange(unit: HEIGHT_UNITS, height?: number): void {
    setQuotation({
      borrower: { height, heightUnit: unit },
    });
  }

  function onWeightChange(unit: WEIGHT_UNITS, weight?: number): void {
    setQuotation({
      borrower: { weight, weightUnit: unit },
    });
  }

  return (
    <Grid container spacing={2} justify='center'>
      <Grid container item xs={12} sm={11}>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <CardRadioButtons 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_GENDER}
            onClick={onGenderSelected}
            options={options}
            value={quotation.borrower.gender}
          />
        </Grid>
        <Grid item xs={12} classes={{item: classes.bottomSpacing}}>
          <DateField 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_BIRTH}
            onChange={onBirthDateSelected}
            value={quotation.borrower.birthDate}
          />
        </Grid>
        <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
          <WeightField 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_WEIGHT}
            onChange={onWeightChange}
            value={quotation.borrower.weight}
            unit={quotation.borrower.weightUnit || WEIGHT_UNITS.POUNDS}
          />
        </Grid>
        <Grid item xs={12} sm={6} classes={{item: classes.bottomSpacing}}>
          <HeightField 
            label={STRINGS.QUOTATION.PERSONAL_LOAN.PROFILE.CHOOSE_HEIGHT}
            onChange={onHeightChange}
            value={quotation.borrower.height}
            unit={quotation.borrower.heightUnit || HEIGHT_UNITS.FEETS}
          />
        </Grid>
      </Grid>
    </Grid>
  );
};

export default Profile;