import React from 'react';
import Grid from '@material-ui/core/Grid';

import STRINGS from '../../../../../strings';
import { BankRadioButtons } from '../../../../../components/RadioButtons';
import { BacLogoImg, MultibankLogoImg, StGeorgeLogoImg, GlobalBankLogoImg, FinancomerLogoImg } from '../../../../../components/Images';
import { LaboralEntity, LaboralSector } from './index';
import { Props as QuotationProps } from '../index';

let options:any = [{}]

interface Props extends QuotationProps {
  laboralEntitys: LaboralEntity[];
  sectors: LaboralSector[];
};

function Banks(props: Props) {
  const { quotation, setQuotation, laboralEntitys, sectors } = props;

  function onOptionPressed(bank: string) {
    setQuotation({ loan: { bank } });
  }
  
  const calcBank = (laboralEntity: LaboralEntity) => {

    let { 
      ruta,
      tasa,
      comision } = laboralEntity 

    // Estos valores deben venir de parametros
    const letraRetenida = false
    const gastoLegal = 0
    const timbres = 0
    const servicioDescto = 0
    const itbms = 7

    const { amount = 0, term = 0, paysYear = 0, 
          financ1 = 0, 
          discount_capacity = 0, 
          termf1 = 0 } = quotation.loan;
    const tasaMes = tasa/(paysYear ? paysYear : 1)/100
    // const factor = (1-(1+tasaMes)**(-term))/tasaMes
    const reFactor = tasaMes/(1-(1+tasaMes)**(-term))

    const monthlyFee = Math.round(reFactor*amount*100)/100

    const comisClose = Math.round(comision*amount)/100
    const comisItbm = Math.round(comisClose*itbms)/100
    const montoLetRet = letraRetenida ? monthlyFee : 0
    const cashOnHand = amount - (comisClose+comisItbm+gastoLegal+timbres+servicioDescto+montoLetRet)

    const available = {
      'aRecibir': cashOnHand,
      financ1, 
      'letraRetenida': letraRetenida,
      'monthlyFee': ruta==='600' ? discount_capacity : monthlyFee,
      'comisClose': comisClose,
      'comisItbm': comisItbm,
      'gastoLegal': gastoLegal,
      'timbres': timbres,
      'servicioDescto': servicioDescto,
      'montoLetRet': montoLetRet,
    }
    console.log(available);

    options.push(
      {
        logo: ruta==='200' ? BacLogoImg : (ruta==='300' ? MultibankLogoImg : (ruta==='400' ? StGeorgeLogoImg : (ruta==='500' ? GlobalBankLogoImg : FinancomerLogoImg ))),
        value: ruta,
        amount: ruta==='600' ? financ1 : cashOnHand,
        payments: ruta==='600' ? termf1 : term,
        monthlyFee: ruta==='600' ? discount_capacity : monthlyFee
      }
    ) 
  }
  
  let { jobSector, profession = 0 } = quotation.borrower
  const idSectors = sectors.filter(st => st.sector === jobSector)
  const idSector = idSectors[0].id_sector

  const finance = localStorage.getItem('financomer-income') || localStorage.getItem('financomer-historial') || localStorage.getItem('financomer-prev-jobs') ||  localStorage.getItem('financomer-prev-jobs') || localStorage.getItem('financomer-status') || ''
  
  let laboralEntities;
  if(finance === ''){
    laboralEntities = laboralEntitys.filter(
      le => le.id_sector === idSector && 
      le.id_profesion === Number(profession) && le.ruta !== '100')
  }else{
    laboralEntities = laboralEntitys.filter(
      le => le.id_sector === idSector && 
      le.id_profesion === Number(profession) && le.ruta === '600')
  }
  console.log(laboralEntities, idSector, profession)

  options=[]
  laboralEntities.forEach(le => {
    calcBank(le)
  })

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <BankRadioButtons
          label={STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_BANK}
          options={options}
          onClick={onOptionPressed}
          value={quotation.loan.bank}
        />
      </Grid>
    </Grid>
  );
};

export default Banks;