import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface LaboralEntity {
  ruta: string,
  id_sector: number,
  id_profesion: number,
  discount_capacity: number;
  discount_capacity_mortgage: number;
  debt_capacity: number;
  debt_capacity_mortgage: number;
  plazo_max: number;
  tasa: number;
  comision: number;
  mount_min: number;
  mount_max: number;
}

export interface LaboralSector {
  id: number;
  sector: string;
  id_sector: number,
  name: string;
}

function Banks(props: Props) {
  const { data: laboralEntitys, 
    loading 
  } = useFetch<LaboralEntity[]>("/laboral_sector_entity_f",{ method: "GET" },[]);

  const {
    data: sectorResponse,
    loading: sectorLoading,
  } = useFetch<LaboralSector[]>('/laboral_sector', { method: 'GET' }, []);


  if (loading || !laboralEntitys || sectorLoading || !sectorResponse) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form 
    {...props} 
    laboralEntitys={laboralEntitys}
    sectors={sectorResponse}
  />
};

export default Banks;