import React from 'react';
import useFetch from 'use-http';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';

import Form from './Form';

export interface iApc {
  institucion:string;
  referencia: string;
  tipoObligacion: string;
  saldoActual: number;
  letra: number;
  formaPago: string;
  diasAtraso: string;
  contometro: string;
  score: string;
  montoAplicado: number;
  montoARecibir: number;
}


function Aproach(props: Props) {
  const { data: refApc, loading } = useFetch<iApc[]>(
    "/APC",
    { method: "GET" },
    []
  );

  if (loading || !refApc) {
    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return <Form {...props} refApc={refApc} />;
};

export default Aproach;